body {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background-position: center;
  background-repeat: no-repeat;
}
#root {
  background-image: url('/hoki.png'); /* Olettaen ett채 'hoki.png' on suoraan 'public' kansiossa */
  background-size: 100%;
  background-position: top;
  background-repeat: no-repeat;
}

.header {
  background-image: url('/hoki.png'); /* Olettaen ett채 kuva on suoraan public-kansiossa */
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
}

.team-container {
  display: flex;
  gap: 16px;
  margin-bottom: 20px;
  padding: 20px;
  border-radius: 18px;
  background-size: cover;
}

.conference {
  flex: 1;
  max-width: 350px;
}

.team-box {
  position: relative;
  padding: 10px;
  border: 1px solid #000000;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  border-radius: 5px;
}

.team-box p {
  margin: 0;
  font-weight: bold;
}
.team-box:hover {
  transform: scale(1.05);
  transition: transform 0.3s ease-in-out;
}


.team-box.selected {
  background-color: #4caf50; /* Vihre채 v채ri valituille joukkueille */
  color: white;
}
.team-box, .simulate-match-popup {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.selected-icon {
  color: white;
}

.simulate-match-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.9);
  padding: 20px;
  border: 1px solid #000000;
  z-index: 1000;
  text-align: center;
}


.button-container {
  margin-top: 10px;
}

.results-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
}

.results-container h2 {
  color: #004d40;
}

.results-container .results-area {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.results-container .results-area h2 {
  color: #ff5722;
}
